import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  loading: true,
  showErrorNotPokemonFound: false
};

export const uiSlice = createSlice({
  name: 'ui',
  initialState,
  reducers: {
    setLoading: (state, action)  => {
      state.loading = action.payload
    },
    setShowErorPokemonNOtFount: (state, action) => {
      state.showErrorNotPokemonFound = action.payload
      console.log(" ~ file: uislice.js:17 ~  state.showErrorNotPokemonFound:",  state.showErrorNotPokemonFound)
    }
  }
})

export const { setLoading, setShowErorPokemonNOtFount } = uiSlice.actions;

export default uiSlice.reducer;