/* eslint-disable react/jsx-pascal-case */
//React
import { useEffect, useState } from 'react';

//React-Redux
import { shallowEqual, useDispatch, useSelector } from 'react-redux';

//Ant Desing
import { Affix, Button } from 'antd';
import { Col } from 'antd';

//Components
import { Searcher } from './components/Searcher';
import { PokemonList } from './components/PokemonList';

//SVG
import logo from './static/logo.svg';

//API
import { LoadingPage } from './components/LoadingPage';
import { PokemonNotFound } from './components/PokemonNotFound';
import { NoPokemonFav } from './components/NoPokemonFav';
import { StarButton } from './components/StartButton';

//Slices
import { fetchPokemonsWithDetails } from './slices/dataSlice';
import { setLoading } from './slices/uiSlice';

function App() {
  //Nos traemos del store el valor que necesitamos
  const pokemons = useSelector(state => state.data.pokemons,shallowEqual);
  const favorites = useSelector(state => state.data.favorites,shallowEqual);
  const loading = useSelector(state => state.ui.loading,shallowEqual);
  const searchedPokemons = useSelector(state => state.data.searchedPokemons,shallowEqual);
  const inputValue = useSelector(state => state.data.inputValue,shallowEqual);
  const showErrorNotPokemonFound = useSelector(state => state.data.showErrorNotPokemonFound,shallowEqual);

  //Indicarle al store que accin se quiere realizar:
  const dispach = useDispatch();

  useEffect(() => {
    dispach(setLoading(true))
    dispach(fetchPokemonsWithDetails())
    setTimeout(() => {
      dispach(setLoading(false))
    }, 1000);
  },[])
  const [ showPage, setShowPage] = useState("pokemonsList");

  const handleShowPage = ()=> {
    if(showPage === "pokemonsList"){
      setShowPage('favPokemonsList')
    }else{
      setShowPage('pokemonsList')
    }
  }

  const pc_or_phone_device = () => {
    if (window.innerWidth <= 630) {
      return false
    }else{
      return true
    }
  }
  return (
    <div className='mt-5 mb-5'>
      {loading
        ? <LoadingPage />
        : <div className='flex flex-col items-center'>
            <Col className='absolute left-6 w-40 '>
              <img  src={logo} alt='Pokedux'/>
            </Col>
            <Col className='mt-12'>
              <Searcher showPage={showPage} active={favorites.length === 0 && showPage === 'favPokemonsList' ? true : false} />
            </Col>
            {pc_or_phone_device()
                ? <Col className='flex justify-end w-[70%] mt-5'>
                    <Button className='' onClick={() => handleShowPage()}>{showPage !== 'pokemonsList' ? 'PokemonsList': 'Favorites'}</Button>
                  </Col>
                : <Affix offsetTop={95} style={{ position: 'absolute', right: '40px', top:'95px' }} >
                    <StarButton  onClick={() => handleShowPage()}/>
                  </Affix>
            }
            <Col>
              {showPage === 'pokemonsList'
                ? inputValue.length === 0
                  ? <PokemonList pokemons={pokemons}/>
                  : <PokemonList pokemons={searchedPokemons}/>
                : inputValue.length === 0
                  ? favorites.length === 0
                    ? <NoPokemonFav/>
                    : <PokemonList pokemons={favorites} />
                  : <PokemonList pokemons={searchedPokemons}/>
              }



              {/* {showPage === 'favPokemonsList'
                ? !inputValue
                  ? favorites.length === 0
                    ? <NoPokemonFav/>
                    : <PokemonList pokemons={favorites} />
                  : <PokemonList pokemons={searchedPokemons}/>
                : !inputValue   //aca hay un problema
                  ? <PokemonList pokemons={pokemons} />
                  : <PokemonList pokemons={searchedPokemons}/>
              } */}
            </Col>
            <Col className='justify-center items-center mt-16'>
              {showErrorNotPokemonFound ? <PokemonNotFound/> : ''}
            </Col>
          </div>
      }
    </div>
  );
}


export default (App);
